/* --- 01 TYPOGRAPHY SYSTEM

      FONT WEIGHT
       Default: 400
      LINE HEIGHTS
      Default:1
      small: 1.05
      paragraph default: 1.6
      FONT SIZE SYSTEM (px)
      10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 / 52 / 62 / 74 / 86 / 98

      --- 02 COLORS

      Primary: #e67e22

