.product-sidebar-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 2rem;
  max-width: 100%;
}

.product-sidebar-header-container {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}

.product-price {
  color: rgba(204, 0, 0);
  font-size: 2.3rem;
}

.sale {
  font-weight: 350;
  font-size: 1.4rem;
}

.reg-product-price {
  font-size: 1.2rem;
  font-weight: 400;
  color: rgb(105, 105, 105);
}

.purchased-online {
  position: relative;
  font-size: 1.2rem;
  line-height: 2.5rem;
  color: rgb(51, 51, 51);
}

#info-btn {
  position: absolute;
  top: 0.3rem;
  margin-left: 0.4rem;
  max-width: 1.9rem;
}

.stars {
  max-width: 10rem;
  margin-left: -1rem;
}

.ratings {
  display: flex;
  align-items: center;
}

.ratings:hover {
  text-decoration: none;
}

.ratings-container {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  gap: 0.2rem;
  color: rgb(105, 105, 105);
  font-size: 1.2rem;
  line-height: 2rem;
  padding-right: 0.4rem;
}

#grey-arrow {
  max-width: 1.6rem;
}

.questions {
  font-size: 1.2rem;
  color: rgb(105, 105, 105);
  border-left: 1px solid rgb(214, 214, 214);
  display: inline-block;
  margin-left: 0.4rem;
  padding-left: 0.4rem;
}

.questions:hover {
  color: black;
  text-decoration: none;
}
